.landing-page {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh; /* 화면 높이 전체를 사용합니다. */
  background: url('../flat-lay-2583213_1280.jpg') center/cover rgba(245, 245, 220, 0.7); /* 배경 이미지와 투명도 설정 */
}

.container {
  padding: 20px;
  text-align: left;
}

.container h1 {
  font-size: 36px;
  margin-bottom: 16px;
  color : rgb(95, 184, 88);
  opacity: 0; /* 초기에 투명하게 설정 */
  animation: fadeInAnimation 2s ease-in-out forwards; /* 페이드인 애니메이션 적용 */
  margin-left : 865px;
  margin-bottom: 100px;

}
.container h2 {
  font-size: 24px;
  margin-bottom: 16px;
  color : rgb(0, 0, 0);
  opacity: 0; /* 초기에 투명하게 설정 */
  animation: fadeInAnimation 2s ease-in-out forwards; /* 페이드인 애니메이션 적용 */
  margin-left : 800px;
  text-align: center;
}
@keyframes fadeInAnimation {
  0% {
      opacity: 0;
  }
  100% {
      opacity: 1;
  }
}

.custom-button {
  border-radius: 20px; /* 둥근 가장자리 설정 */
  background-color: rgb(255, 255, 255); /* 주황색 배경색 */
  color: rgb(0, 0, 0); /* 흰색 텍스트 색상 */
  padding: 10px 20px; /* 내부 여백 설정 */
  border: none; /* 테두리 없애기 */
  cursor: pointer; /* 포인터 모양 변경 */
  margin-top: 30px;


}

.custom-button:hover {
  background-color: rgb(191, 190, 189); /* 마우스 호버 시 색상 변경 */
}
